import React, { useState, useEffect } from "react";
import axios from "axios";

import Chat from './Chat.js'
import TextArea from './TextArea.js'

function ChatWrap() {

  /* textArea 채팅값 저장  */
  const [chat, setChat] = useState("");
  /* response값 저장소 */
  const [chats, setChats] = useState([])

  /* chat을 back에 보내기위해 형식 맞춰줌 */
  const forSendChats = {
    role: "user",// user는 예시임
    content: chat,
  }

  useEffect(() => { console.log(chats) }, [chats]);

  /* 채팅 back으로 보내기, response를 chat에 추가하기 */
  async function sendChat() {
    try {

      /* back에 채팅 보내기 */
      const chatResponse = await axios.post("/api/chat", forSendChats);

      /* chats에 넣기전에 형식 맞춰줌 */
      const forResponseChats = {
        role: "Gpt",
        content: chatResponse.data,
      }

      /* chats 채팅리스트에 넣기 */
      const chatsCopy = [...chats];
      /* user채팅 넣기 */
      chatsCopy.push(forSendChats);
      /* Gpt채팅 넣기 */
      chatsCopy.push(forResponseChats);
      setChats(chatsCopy);

    } catch (error) {
      console.log(error);
    }
  }

  return (
    <div className="chatWrap">

      {/* 채팅내역 출력 */}
      {chats.map(function (a) {
        return (
          a.role === "user" ? (
            <Chat bg='#D6C6E1' name='user' text={a.content}></Chat>
          ) : (<Chat bg='#9A73B5' name='Gpt' text="1234"></Chat>))
      })}

      {/* textArea */}
      <TextArea chats={chats} setChat={setChat} setChats={setChats} sendChat={sendChat} />
    </div>
  );
}
export default ChatWrap;